.body {
  display: flex;
  flex-direction: column;
  align-content: center;
  text-align: center;
}

.article {
  font-size: 1.5rem;
  text-align: center;
  margin-bottom: 1rem;
}

.checkInfluencerContainer {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.button {
  display: flex;
  justify-content: center;
  margin-bottom: 3rem;
}

.spinner {
}

.tweetListBox {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media screen and (max-width: 768px) {
  .article {
    font-size: 1rem;
  }
}
